.m-num-operator{
	display: -ms-flexbox;
	display: flex;
	width: 1.8rem;
	height: .5rem;
	border: 0.01rem solid #BDBDBD;
	border-radius: .1rem;
}

.m-num-operator button{
	height: .5rem;
	line-height: .5rem;
	width: .5rem;
	background: none;
	border: none;
}

.m-num-operator button:first-child{
	border-right: 0.01rem solid #BDBDBD;
}

.m-num-operator button:last-child{
	border-left: 0.01rem solid #BDBDBD;
}

.m-num-operator input{
	width: .6rem;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	text-align: center;
	font-size: .25rem;
	border: none;
}


