.page-balance-detail .m-trans-item{
	display: -ms-flexbox;
	display: flex;
	background-color: #fff;
	padding: .23rem .3rem;
	border-bottom: 0.01rem solid #f1f1f1;
}
.page-balance-detail .m-trans-item .m-left-wrapper{
	width: 70%;
	-ms-flex-positive: 0;
	    flex-grow: 0;
}
.page-balance-detail .m-trans-item .m-left-wrapper .u-title{
	font-size: .28rem;
	white-space: nowrap;
	overflow: hidden;
	-ms-text-overflow: ellipsis;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	color: #333;
}
.page-balance-detail .m-trans-item .m-left-wrapper .u-money{
	font-size: .26rem;
	color: #aaa;
	margin-top: .1rem;
}
.page-balance-detail .m-trans-item .m-right-wrapper{
	-ms-flex-preferred-size: 1;
	    flex-basis: 1;
	text-align: right;
	font-size: .32rem;
	font-weight: bold;
	width: 30%;
	color: #333;
}

.page-balance-detail .m-trans-item .m-right-wrapper.income{
	color: #f13030;
}


