.m-page-result{

}
.m-page-result .m-goods-list{
	/*margin-top: .1rem;*/
}

.m-sortbar{
	display: -ms-flexbox;
	display: flex;
	height: .9rem;
	line-height: .9rem;
	-ms-flex-align: stretch;
	    align-items: stretch;
	background-color: #fff;
	border-top: 0.01rem solid #CDCDCD;
	border-bottom: 0.01rem solid #CDCDCD;
}

.m-sortbar .u-sort-item{
	line-height: inherit;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	-ms-flex-preferred-size: 25%;
	    flex-basis: 25%;
	text-align: center;
}

.m-sortbar .u-sort-item.is-active{
	color: #f13030;
}

.m-sortbar .u-sort-price{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.m-sortbar .u-sort-price .m-pt-box{
	display: inline-block;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	margin-left: .1rem;
}

.m-sortbar .u-sort-price .m-pt-box .u-pt{
	-ms-flex-align: center;
	    align-items: center;
	content: "";
	display: block;
	border: .1rem solid transparent;
}

.m-sortbar .u-sort-price .m-pt-box .u-pt-up{
	border-bottom-color: #cdcdcd;
}

.m-sortbar .u-sort-price .m-pt-box .u-pt-up.is-active{
	border-bottom-color: #f13030;
}

.m-sortbar .u-sort-price .m-pt-box .u-pt-down{
	border-top-color: #cdcdcd;
	margin-top: .05rem;
}
.m-sortbar .u-sort-price .m-pt-box .u-pt-down.is-active{
	border-top-color: #f13030;
}




