.m-index-searchbar{
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
}

.m-index-searchbar .am-search{
	background-color: transparent;
	background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,.2)),to(rgba(0,0,0,.1)));
	background-image: -webkit-linear-gradient(top,rgba(0,0,0,.2),rgba(0,0,0,.1));
	background-image: -o-linear-gradient(top,rgba(0,0,0,.2),rgba(0,0,0,.1));
	background-image: linear-gradient(to bottom,rgba(0,0,0,.2),rgba(0,0,0,.1));
}

.m-index-searchbar .am-search-input{
	/*background-color: rgba(255,255,255,.5);*/
}

.m-index-searchbar .am-search-cancel{
	color: #FFF;
}


