.m-address-box{
	background-color: #fff;
	padding: .2rem;
	display: -ms-flexbox;
	display: flex;
}
.u-mail-band{
	content: "";
	display: block;
	width: 100%;
	height: .05rem;
	background-image: -webkit-gradient(linear,left top, right top,color-stop(25%, #2f63da),color-stop(25%, #FFF),color-stop(50%, #FFF),color-stop(50%, #de5a48),color-stop(75%, #de5a48),color-stop(75%, #FFF),to(#FFF));
	background-image: -webkit-linear-gradient(left,#2f63da 25%,#FFF 25%,#FFF 50%,#de5a48 50%,#de5a48 75%,#FFF 75%,#FFF 100%);
	background-image: -o-linear-gradient(left,#2f63da 25%,#FFF 25%,#FFF 50%,#de5a48 50%,#de5a48 75%,#FFF 75%,#FFF 100%);
	background-image: linear-gradient(to right,#2f63da 25%,#FFF 25%,#FFF 50%,#de5a48 50%,#de5a48 75%,#FFF 75%,#FFF 100%);
	background-size: 10% 100%;
}
.m-address-box .m-left-wrapper{
	-ms-flex: 1 1 0%;
	    flex: 1 1 0%;
}
.m-address-box .m-right-wrapper{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}
.m-address-box .u-top-name{
	font-size: .3rem;
}
.m-address-box .u-top-name .u-name{
	margin-right: .3rem;
	margin-left: .36rem;
}
.m-address-box .u-top-name .u-mobile{

}
.m-address-box .u-address{
	margin-top: .15rem;
	font-size: .25rem;
}

.m-address-box .u-address .u-text{
	margin-left: .15rem;
	color: #323232;
}

.am-icon-xxxs {
    width: 0.22rem;
    height: 0.22rem;
}

/*订单项*/
.m-goods-box{
	margin-top: .15rem;
}
.m-goods-item-b{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	padding: .1rem .2rem;
	background-color: #fff;
}

.m-goods-item-b .u-logo{
	width: 1.8rem;
	height: 1.8rem;
	border-radius: .03rem;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
}
.m-goods-item-b .m-right{
	margin-left: .15rem;
	-ms-flex-positive: 1;
	    flex-grow: 1;
}
.m-goods-item-b .m-right .u-name{
	font-size: .28rem;
	line-height: .34rem;
	max-height: .68rem;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	overflow: hidden;
}
.m-goods-item-b .m-right .u-bottom{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: end;
	    align-items: flex-end;
	margin-top: .2rem;
	font-size: .27rem;
}
.m-goods-item-b .m-right .u-bottom .u-price{
	color: red;
}

.m-goods-item-b .m-right .u-bottom .u-nums{
	color: #b0b0b0;
}

/**/
.m-money-box{
	background-color: #fff;
	margin-top: 0.15rem;
	padding: .2rem;
}
.m-money-box .m-money-item{
	font-size: .27rem;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.m-money-box .m-money-item:not(:first-child){
	margin-top: .15rem;
}

.m-money-box .m-money-item .u-item-name{

}
.m-money-box .m-money-item .u-item-value{
	color: #f13030;
}

/*结算*/
.page-ordercreate .m-account-bar{
	position: fixed;
	bottom: 0;
}

.m-remarks-wrapper{
	margin-top: .1rem;
}

.m-remarks-wrapper .am-list-item.am-input-item:after{
	border: none;
}


